/* eslint-disable */
function getSearchValueByKey(search, key) {
  return decodeURI(search.replace(new RegExp('^(?:.*[&\\?]' + encodeURI(key).replace(/[\.\+\*]/g, '\\$&') + '(?:\\=([^&]*))?)?.*$', 'i'), '$1'));
}

function replaceQueryParam(url, key, value) {
  var pattern = key + '=([^&]*)';
  var replaceText = key + '=' + value;

  if (url.match(pattern)) {
    var tmp = '/(' + key + '=)([^&]*)/gi';
    tmp = url.replace(eval(tmp), replaceText);
    return tmp;
  } else {
    if (url.match('[?]')) {
      return url + '&' + replaceText;
    } else {
      return url + '?' + replaceText;
    }
  }

  return url + '\n' + key + '\n' + value;
}

export { getSearchValueByKey, replaceQueryParam };